<template>
	<!--  -->
	<div class="divColumn">
		<Title title=""></Title>
		<div class="conTop">
			<div style="margin: 0rem 0.625rem;margin-top: 0.875rem;" class="divColumn">
				<van-field v-model="pwd" placeholder="" label=" :" label-align="left" label-width="80" class="filedWrap" type="password" />
				<van-field v-model="pwdagin" placeholder="" label=":" label-align="left" label-width="84" class="filedWrap" type="password" />

				<div class="divCenter sureBtnWrap" @click="sure"></div>
				<div style="width:100%;margin-top: 3rem;text-align: left;font-size: 0.8125rem;" class="divColumn">
					<span style="color: red;"></span>
					<p>*</p>
					<span>*</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
export default {
	data() {
		return {
			pwd: '',
			pwdagin: '',
			address: '',
			secret: ''
		};
	},
	components: {
		Title
	},
	created: function() {
		var _this = this;
		_this.address = _this.$route.query.address;
		_this.secret = _this.$route.query.secret;
	},
	methods: {
		sure: function() {
			var _this = this;
			if (_this.pwd == '') {
				_this.$toast('');
				return;
			}
			if (_this.pwdagin == '') {
				_this.$toast('');
				return;
			}
			if (_this.pwd != _this.pwdagin) {
				_this.$toast('');
				return;
			}
			_this.$toast.loading({
				message: '',
				forbidClick: true,
				duration: 0
			});
			var data = {
				pwd: encodeURIComponent(_this.pwd),
				address: _this.address,
				secret: _this.secret
			};
			_this.saveWallent(data);
			_this.$toast.clear();
			_this.$router.go(-3);
		}
	}
};
</script>

<style>
.filedWrap {
	background-color: #f6f8fe;
	padding-left: 0rem;
	padding-right: 0rem;
	border-radius: 0.3125rem;
	margin-bottom: 1.5rem;
}
</style>
