import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

import axios from 'axios'
import qs from 'qs'
Vue.prototype.$ajax = axios
Vue.prototype.$qs = qs
// axios.defaults.headers.post['Content-Type'] = 'application/json';
//
import gloabal from '@/api/global_variable.js'
Vue.prototype.gloabal = gloabal;

//
import basaJ from './base'
Vue.use(basaJ); //
// K
import kline from 'jcc_kline'
Vue.use(kline)
//
import moment from 'moment/moment'
Vue.prototype.$moment = 'moment'
Vue.filter('moment', function(value, formatString) {
	formatString = formatString || 'YYYY-MM-DD HH:mm:ss';
	return moment.unix(value).format(formatString); // 
});
// 
import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)

// 
//
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)
import messages from './assets/lang/index.js'

var defaultLang = localStorage.getItem('lang')
if (defaultLang == null) {
	defaultLang = 'ch';
	localStorage.setItem('lang', 'ch')
}
const i18n = new VueI18n({
	//
	locale: defaultLang,
	messages,
	// silentTranslationWarn: true,
})






import {
	Button,
	Field,
	RadioGroup,
	Radio,
	Tabbar,
	TabbarItem,
	NoticeBar,
	Grid,
	GridItem,
	Image,
	List,
	PullRefresh,
	Slider,
	Tab,
	Tabs,
	Dialog,
	Toast,
	Cell,
	CellGroup,
	Overlay,
	Popup,
	Uploader,
	Step,
	Steps,
	Swipe,
	SwipeItem,
	Switch,
	Search,
	DropdownMenu,
	DropdownItem,
	ActionSheet
} from 'vant';
Vue.use(Button);
Vue.use(Field);
Vue.use(RadioGroup);
Vue.use(Radio);
Vue.use(Tabbar);
Vue.use(TabbarItem);
Vue.use(NoticeBar);
Vue.use(Grid);
Vue.use(GridItem);
Vue.use(Image);
Vue.use(List);
Vue.use(PullRefresh);
Vue.use(Slider);
Vue.use(Tab);
Vue.use(Tabs);
Vue.use(Dialog);
Vue.use(Toast);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Overlay);
Vue.use(Popup);
Vue.use(Uploader);
Vue.use(Step);
Vue.use(Steps);
Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Switch);
Vue.use(Search);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(ActionSheet)

//css
import './css/commontCust.css'
Vue.config.productionTip = false;
//
import api from './api.js'
import md5 from 'js-md5';
Vue.use(api,md5); //
new Vue({
	router,
	store,
	i18n,
	render: h => h(App)
}).$mount("#app");
