<template>
	<!-- -->
	<div class="divColumn">
		<Title title=""></Title>
		<div class="conTop divColumn">
			<div style="margin: 0rem 0.625rem;" class="divColumn">
				<div class="leftAndRight itemW">
					<span style="width: 30%;"></span>
					<van-field style="background-color:#f6f8fe;" v-model="secret" placeholder=""></van-field>
				</div>
				<div class="leftAndRight itemW">
					<span style="width: 30%;"></span>
					<van-field style="background-color:#f6f8fe;" v-model="pwd" placeholder="" type="password"></van-field>
				</div>
				<div
					@click="sureUpd"
					class="itemW divCenter"
					style="background-color: #518CFF;border-radius: 0.3125rem;margin-top: 2.5rem;font-size: 1rem;font-weight: 600;color: white;"
				>
					<span></span>
				</div>
				<!--  -->
				<div class="divColumn" style="text-align: left;margin-top: 1.25rem;margin-bottom: 2.5rem;">
					<span style="margin-bottom: 0.625rem;"></span>
					<span style="text-indent:2em;line-height: 1.5rem;">*/</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
export default {
	components: {
		Title
	},
	data() {
		return {
			pwd: '',
			secret: '',
			walData: {}
		};
	},
	created: function() {
		var _this = this;
		_this.walData = _this.getWallent();
	},
	methods: {
		sureUpd: function() {
			var _this = this;
			if (_this.secret == '') {
				_this.$toast.fail('');
				return;
			}
			if (_this.pwd == '') {
				_this.$toast.fail('');
				return;
			}
			if (_this.pwd != _this.walData.pwd) {
				_this.$toast.fail('');
				_this.pwd = '';
				return;
			}
			var data = {
				secret_ro: _this.secret
			};
			_this.saveWallent(data);
			_this.$toast.success('');
			_this.$router.go(-1);
		}
	}
};
</script>

